@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/notes/static/media/Blackboard2.3ddc63cd.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h2 {
  color: white;
  padding: 30px;
  font-family: 'Dancing Script', cursive;

}



.todos {
    position: absolute;
    margin-top: -205px;
    margin-left: 6vw;
}

.enter {
    text-align: center;
}

.wide {
    overflow: hidden;
}

.enter input {
    text-align: center;
    border-radius: 10px;
    width: 80vw;
    font-size: 20px;
}

.enter button {
    display: inline;
    box-sizing: border-box;
    border-radius: 20px;
}

textarea {
    font-family: 'Dancing Script', cursive;
    border-radius: 10px;
}

input {
    font-family: 'Dancing Script', cursive;
}
.todos textarea {
    background-color: transparent;
    border-color:transparent;
    font-family: 'Dancing Script', cursive;
    font-size: 24px;
}

.close {
    color: red;
}

h2 {
    text-align: center;
    font-size: 56px;
}

.block{
    position:absolute;
    display:none;
    margin-left: 175px;
    margin-top: -215px;
    color: red;
}
.notes:hover .block{
    display:block;
}

.notes img {
    margin-top: 40px;
    margin-left: 5vw;
    object-fit: cover;
    height:250px;
}

.btn-grd-primary {
    background-size: 200% auto;
    transition: 0.5s ease-in-out;
    color: #fff;
    font-family: 'Dancing Script', cursive;
}

.btn-grd-primary:hover {;
    background-position: right center;
}

.btn-grd-primary.hor-grd {
    background-size: auto 200%;
}

.btn-grd-primary.hor-grd:hover {
    background-position: bottom center;
}

.btn-grd-primary {
    background-image: linear-gradient(to right, #77aaff 0%, #0764ff 51%, #77aaff 100%);
}
.btn-grd-primary.hor-grd {
    background-image: linear-gradient(to top, #77aaff 0%, #0764ff 51%, #77aaff 100%);
}


